@font-face {
    src:
        local("Quicksand-Regular"),
        url("../fonts/Quicksand-Regular.woff2") format("woff2"),
        url("../fonts/Quicksand-Regular.woff") format("woff");
    font-family: "Quicksand-Regular";
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

